<template>
<div class="background">
<div class="window">
    <h1>Image Optimizer</h1>
    <div class="input_box">
      <input class="input" type="text" v-model="Input_Path" />
      <button class="btn" @click="select('input')">Input</button>
    </div>
    <div class="input_box">
      <input class="input" type="text" v-model="Output_Path" />
      <button class="btn" @click="select('output')">Output</button>
    </div>

    <div class="size-box">
      <div>
        <div class="input_box_small">
          <input type="number" placeholder="2560" v-model="new_size" class="input" />
          <button class="btn" @click="addSize">add</button>
        </div>
        <ul class="size-list">
          <li v-for="(size, idx) in Sizes" :key="idx"><span>{{ size }}px</span> <span @click="removeSize(idx)" class="remove">X</span></li>
        </ul>
      </div>
      <button class="btn btn-start" @click="make_images">make images</button>
    </div>
  </div>
</div>
  
</template>

<script>
export default {
  name: 'App',
  data() {
    return {
      Input_Path: 'Input Path',
      Output_Path: 'Output Path',
      Sizes: [1920, 1440, 1024, 960, 800, 600, 320, 150],
      new_size: null,
    };
  },
  methods: {
    async select(folder) {
      console.log(folder);
      // Test
      // let Selected_Folder = `/${folder}/`;

      let Selected_Folder = await eel.Select_Folder(folder)();
      if (folder === 'input') {
        this.Input_Path = Selected_Folder + '/';
      } else {
        this.Output_Path = Selected_Folder + '/';
      }
    },
    async make_images() {
      // let result = 'ewjkhejkw';
      let result = await eel.make_images(this.Input_Path,this.Output_Path, this.Sizes)()
      console.log(result);
    },
    addSize() {
      let Size_list = this.Sizes;
      let Add_Size = parseInt(this.new_size);
      console.log(!isNaN(Add_Size), Add_Size);
      !isNaN(Add_Size) ? Size_list.push(this.new_size) : '';
      Size_list.sort((a, b) => b - a);
      this.Sizes = Size_list;
    },
    removeSize(index) {
      this.Sizes.indexOf(index);
      if (index > -1) {
        this.Sizes.splice(index, 1);
      }
    },
  },
};
</script>

